<template>
  <div class="search">
    <div class="search-box">
      <van-search
        v-model="value"
        shape="round"
        background="#fefefe"
        placeholder="请输入搜索关键词"
        @input="onInput"
        @clear="toggleBigBox"
        @search="getResultList"
      />
    </div>

    <template v-if="isSure">
      <div class="hotSearch" v-if="show">
        <p>热门搜索</p>
        <ul class="hotUl">
          <li
            v-for="(hot,index) in hotSearch"
            :key="index"
            class="hotLi"
            @click="gethotSearch(hot)"
          >{{hot.first}}</li>
        </ul>
        <ul class="searchUl">
          <li class="searchLi" v-for="(item,index) in historyList" :key="index" @click="getLiSearch(item)">
            <div class="icon icon1"></div>
            <div class="item">
              {{item}}
              <div class="icon del" @click.stop="toDelHistury(index)"></div>
            </div>
          </li>
        </ul>
      </div>
      <ul class="searchUl" v-else>
        <li
          class="searchLi"
          v-for="(item,index) in findOutList"
          :key="index"
          @click="getHSearch(item)"
        >
          <div class="icon icon2"></div>
          <div class="item">{{item.keyword}}</div>
        </li>
      </ul>
    </template>

    <template v-else>
      <div class="list">
        <NewSongList
          v-for="(newSong,index) in resultList"
          :key="index"
          :newSongItem="newSong"
          :songInfo="{isShowInfo:true,newSongItem:newSong,isSearch:index}"
          @current-song="$emit('current-song', $event)"
        ></NewSongList>
      </div>
    </template>
  </div>
</template>

<script>
import NewSongList from "@/components/NewSongList.vue";

export default {
  name: "Search",
  data() {
    return {
      value: "",
      hotSearch: [],
      findOutList: [],
      resultList: [],
      historyList: [],
      show: true,
      isSure: true,
      isSearch: ""
    };
  },
  components: { NewSongList },
  methods: {
    //热门搜索数据
    getHotSearch() {
      this.axios
        .get("/search/hot")
        .then(response => {
          // console.log(response);
          this.hotSearch = response.data.result.hots;
          this.firstSix();
          // 获取到的数据放入本地存储
          window.localStorage.setItem(
            "hotSearch",
            // 过期时间1小时
            JSON.stringify({
              expire: Date.now() + 1 * 60 * 60 * 1000,
              result: response.data.result.hots
            })
          );
        })
        .catch(error => {
          error;
        });
    },

    //搜索建议
    onInput() {
      this.show = false;
      this.axios
        .get("/search/suggest", {
          params: {
            keywords: this.value,
            type: "mobile"
          }
        })
        .then(response => {
          this.findOutList = response.data.result.allMatch;
        })
        .catch(error => {
          error;
        });
    },

    //最终数据
    getResultList() {
      this.isSure = false;
      this.show = false;
      this.toOnly(this.historyList, this.value);
      const cacheResultList = JSON.parse(
        window.localStorage.getItem("resultList" + this.value)
      );
      if (cacheResultList && cacheResultList.expire > Date.now()) {
        // 存在并且还没有过期
        this.resultList = cacheResultList.result;
      } else {
        // 已经过期
        this.axios
          .get("/search", {
            params: {
              keywords: this.value
            }
          })
          .then(response => {
            this.resultList = response.data.result.songs;
            window.localStorage.setItem(
              "resultList" + this.value,
              // 过期时间1小时
              JSON.stringify({
                expire: Date.now() + 1 * 60 * 60 * 1000,
                result: response.data.result.songs
              })
            );

            window.localStorage.setItem(
              "historyList",
              JSON.stringify(this.historyList)
            );
          })
          .catch(error => {
            error;
          });
      }
    },

    gethotSearch(v) {
      this.value = v.first;
      this.getResultList();
    },

    getHSearch(v) {
      this.value = v.keyword;
      this.getResultList();
    },

    getLiSearch(v) {
      this.value = v;
      this.getResultList();
    },

    toggleBigBox() {
      this.show = true;
      this.isSure = true;
    },

    //删除历史记录
    toDelHistury(n) {
      this.historyList.splice(n, 1);
      window.localStorage.setItem(
        "historyList",
        JSON.stringify(this.historyList)
      );
    },

    toOnly(arr, value) {
      if (arr.indexOf(value) == -1) {
        arr.unshift(this.value);
      }
    }
  },
  created() {
    const cacheHotSearch = JSON.parse(window.localStorage.getItem("hotSearch"));
    if (cacheHotSearch && cacheHotSearch.expire > Date.now()) {
      // 存在并且还没有过期
      this.hotSearch = cacheHotSearch.result;
    } else {
      // 已经过期
      this.getHotSearch();
    }

    const cacheHistoryList = JSON.parse(
      window.localStorage.getItem("historyList")
    );
    if (cacheHistoryList) {
      // 存在
      this.historyList = cacheHistoryList;
    } else {
      this.historyList = [];
    }
  }
};
</script>

<style lang="less" scoped>
.list {
  padding-bottom: 47px;
}
.search-box {
  border-bottom: 1px solid #dedede;
}
ul {
  list-style: none;
}
.hotSearch {
  padding: 0 10px;
  p {
    line-height: 32px;
    font-size: 14px;
  }
  .hotUl {
    overflow: hidden;
    margin-bottom: 20px;
  }
  .hotLi {
    padding: 3px 10px;
    border: 1px solid #dedede;
    border-radius: 10px;
    font-size: 16px;
    margin: 5px;
    float: left;
  }
}
.searchLi {
  line-height: 40px;
  font-size: 16px;
  display: flex;
  .icon {
    width: 40px;
    height: 40px;
    background-repeat: no-repeat;
    background-position: center center;
  }
  .item {
    flex: 1;
    border-bottom: 1px solid #dedede;
  }
}
.icon2 {
  background-image: url(../assets/big.png);
}
.icon1 {
  background-image: url(../assets/time.png);
}
.del {
  display: inline-block;
  float: right;
  background-image: url(../assets/del.png);
}
</style>